<template>
  <section class="data_section" ref="data_section">
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="14">
        <logList class="tjHeader"></logList>
      </el-col>
      <el-col :span="10">
        <radarChart class="tjHeader"></radarChart>
      </el-col>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <barChart type="barChart"></barChart>
    </el-row>
  </section>
</template>
<script>
import { getUserList, getSL } from "@/api/user";
import { planeList } from "@/api/plane";
import echarts from "echarts";
import logList from "./components/logList"; // 日志列表
import barChart from "@/components/echarts/barChart"; // 用户能力分析 柱状图
import radarChart from "@/components/echarts/radarChart"; // 用户能力分析 雷达图
export default {
  data() {
    return {
      user: this.sessionData("get", "user")
    };
  },
  components: {
    logList,
    barChart,
    radarChart
  },
  mounted() {
    this.getUserList({
      count: 1000,
      index: 0,
      search: {
        state: 1
      }
    });
    this.planeList({
      count: 1000,
      index: 0,
      search: {
        state: 1
      }
    });
  },
  methods: {
    getUserList(obj) {
      getUserList(obj).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          for (var i in list.array) {
            delete list.array[i].icon;
          }
          this.sessionData("set", "userListAll", list.array);
          var user = this.sessionData("get", "user");
          var arr = [];
          if (user && user.mode == 1) {
            for (var i in list.array) {
              if (list.array[i].state == 1) {
                arr.push(list.array[i]);
              }
            }
            this.sessionData("set", "userList", arr);
          } else {
            for (var i = 0; i < list.array.length; i++) {
              if (user._id == list.array[i]._id) {
                var arr = [list.array[i]];
                this.sessionData("set", "userList", arr);
              }
            }
          }
        }
      });
    },

    planeList(obj) {
      planeList(obj).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          var arr = [];
          for (var i in list.array) {
            if (list.array[i].state == 1) {
              arr.push(list.array[i]);
            }
          }
          this.sessionData("set", "planeList", arr);
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.tjHeader {
  float: left;
}
.data_section {
  margin: 20px;
  border-radius: 2px;
  .row_list {
    margin-bottom: 0px;
    .row_base {
      padding: 10px;
      box-sizing: border-box;
      background: #fff;
      border-radius: 6px;
      height: 120px;
    }
  }
  .order_list {
    .orderArea {
      width: 100%;
      height: 300px;
      border-radius: 6px;
      box-sizing: border-box;
      padding: 10px;
      padding-top: 40px;
      overflow: hidden;
    }
    .orderbarArea {
      height: 550px;
    }
  }
  .data_list {
    text-align: center;
    font-size: 14px;
    border-radius: 6px;
    padding: 10px;
    color: #fff;
    height: 80px;
    .leftItem {
      align-items: start;
      justify-content: space-between;
      text-align: left;
    }
    .rightItem {
      width: 62px;
      display: flex;
      align-items: center;
      justify-content: center;
      .svg-icon {
        font-size: 30px;
      }
    }
    .number {
      font-size: 22px;
      font-weight: bold;
      .perTitle {
        font-size: 13px;
        margin-left: 5px;
      }
    }
  }
  .pay {
    .leftItem {
      justify-content: space-around;
    }
  }
}
</style>
