<template>
  <div class="logContainer">
    <el-card class="box-card">
      <div id="video">
        <!-- <video id="myVideo" width="111" height="100" preload="none" controls="controls">
          <source src="../../../assets/1.mp4" type="video/mp4" />
        </video> -->
        <div v-if="!user.icon"></div>
        <img :src="user.icon" v-else-if="user.icon"/>
      </div>
      <div class="logArea el-scrollbar">
        <div class="item">
          <div class="logContent">
            <ul class="logUl">
              <li>姓名:&nbsp&nbsp&nbsp&nbsp{{user.name}}</li>
              <li>年龄:&nbsp&nbsp&nbsp&nbsp{{user.age}}</li>
              <li>排名:&nbsp&nbsp&nbsp&nbsp{{user.level}}</li>
              <li>胜率:&nbsp&nbsp&nbsp&nbsp{{user.rate*100?(user.rate*100).toFixed(2)+"%":""}}</li>
              <li>对抗次数:&nbsp&nbsp&nbsp&nbsp{{user.fight}}</li>
              <li>最近工作时间:&nbsp&nbsp&nbsp&nbsp</br>{{user.woketime||""}}</li>
            </ul>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import man from "@/assets/img/man.png";
import { getSL } from "@/api/user";
import { github } from "@/utils/env";
export default {
  name: "logList",
  data() {
    return {
      user:{},
      github: github,
      man: man
    };
  },
  mounted() {
    this.getSL();
  },
  methods: {
    getSL() {
      var user = this.sessionData("get", "user");
      var info = {
        "_id": user._id,
      }
      getSL(info).then(res => {
        var data = this.$unfly(res.data);
        if(data.woketime){
          data.woketime = this.$tool.formatDate(new Date(data.woketime),3);
        }
        this.user = data;
        this.sessionData("set", "user",data);
      });
    },
  }
};
</script>
<style lang="less" scoped>
#video img{
    width: 70%;
  height: 408px;
  float: left;
  margin-top: 40px;
}
#video div{
    width: 70%;
  height: 408px;
  float: left;
  margin-top: 40px;
}

#myVideo {
  width: 70%;
  height: 408px;
  float: left;
  margin-top: 40px;
}
.logContainer {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  background: url("../../../assets/img/jbqk.png") no-repeat;
  background-size: 100% 100%;
  height: 496px;
  overflow: hidden;
  border-radius: 6px;
  color: #fff;
  .logArea {
    overflow: auto;
    height: 100%;
  }
  .item {
    .title {
      font-size: 13px;
    }
    .time {
      color: #87de75;
    }
    .logContent {
      .logUl {
        padding-left: 30px;
        float: left;
        margin-top: 80px;
        li {
          font-size: 22px;
          list-style: disc;
          line-height: 20px;
          color: #48ffff;
          list-style: none;
          margin-bottom: 20px;
        }
      }
    }
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
/deep/.el-card {
  border: 0px solid #ebeef5;
  background: none;
}
/deep/.el-card__header {
  border-bottom: 0px solid #ebeef5;
}
/deep/.logArea .item {
  border-bottom: 0px solid #ebeef5;
}

@media screen and (max-width: 1366px) {
  .logContainer .item .logContent .logUl li {
    font-size: 15px;
  }
}
</style>

