<template>
  <div :id="id" class="orderArea"></div>
</template>

<script>
import echarts from "echarts";
export default {
  name: "radarChart",
  data() {
    return {
      id: "radarChart",
      myChart: null
    };
  },
  mounted() {
    this.loadChart();
  },
  beforeDestroy() {
    if (!this.myChart) {
      return;
    }
    this.myChart.dispose();
    this.myChart = null;
  },
  methods: {
    loadChart() {
      this.$nextTick(() => {
        this.myChart = echarts.init(document.getElementById(this.id));
        var user = this.sessionData("get", "user")
        this.myChart.setOption(this.initOption(user));
      });
    },
    initOption(user) {
      var value = [
        user.hitpercent||0,
        user.livepercent||0,
        user.epercent||0,
        user.foulpercent||0,
        user.scorepercent||0,
        user.power_y||0
      ];
      for(var i in value){
        if(i == "0" || i == "1" || i == "2" || i == "3"){
          value[i] = Number(Number(value[i]).toFixed(2))*100;
        }else{
          value[i] = Number(Number(value[i]).toFixed(2));
        }
      }

      let option = {
        title: {
          text: "",
          subtext: "",
          left: "100px",
          top:"0px",
        },
        tooltip: {
            trigger: 'axis'
        },
        legend: {
          data: ["攻击能力", "生存能力", "电子对抗能力", "违规","理论水平","体能"]
        }, 
        radar: [
          {
            name: {
              fontSize: 11
            },
            splitLine: {
              lineStyle: {
                color: "#00aaff"
              }
            },
            axisLabel: {
              inside: true
            },
            axisLine: {
              lineStyle: {
                color: "#00aaff" // 半径线颜色
              }
            },
            splitArea: {
              areaStyle: {
                // color: ['#00aaff', '#0099ff', '#00aaff', '#0099ff', '#00aaff'] // 每一圈的颜色
              }
            },
            indicator: [
              { text: "攻击能力", max: 100, color: "#87DE75" }, // 选中颜色 
              { text: "生存能力", max: 100, color: "#FFA3A1" },
              { text: "电子对抗能力", max: 100, color: "#FF9900" },
              { text: "违规", max: 100, color: "#5FB4FA" },
              { text: "理论水平", max: 100, color: "#a9d86e" },
              { text: "体能", max: 100, color: "#a9d86e" },
            ]
          }
        ],
        series: [
          {
            type: "radar",
            data: [
              {
                value: value,
                areaStyle: {
                  normal: {
                    opacity: 0.8, // 图表透明度
                    color: "#87DE75" // 图表颜色
                  }
                },
                lineStyle: {
                  color: "#6397ff" // 图表边框颜色
                },
                label: {
                  normal: {
                    show: true,
                    color: "#6397ff", // 顶点数字颜色
                    fontSize: 16, // 顶点数字大小
                    formatter: function(params) {
                      return params.value;
                    }
                  }
                }
              }
            ]
          }
        ]
      };
      return option;
    }
  },
  watch: {}
};
</script>
<style lang="less">
#radarChart {
  margin-left: 4px;
  width: 100%;
  height: 496px;
  background: url("../../assets/img/zhll.png") no-repeat;
  background-size: 100% 100%;
  border-radius: 0px;
}
</style>