<template>
  <div class="orderAreaWrapper">
    <el-row>
      <el-col :span="24">
        <el-select
          class="changeData"
          @change="change"
          style="width:403px;"
          v-model="value"
          placeholder="请选择"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <p class="sl"><span></span>胜率(百分百)</p>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div :id="id" class="orderArea orderbarArea"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import echarts from "echarts";
import echartsTheme from "@/components/echarts/theme/westeros.json";
import { level } from "@/api/user";
export default {
  name: "barChat",
  data() {
    return {
      id: "barChart",
      myChart: null,
      options: [],
      value: ""
    };
  },
  mounted() {
    this.init();
    this.option();
  },
  beforeDestroy() {
    if (!this.myChart) {
      return;
    }
    this.myChart.dispose();
    this.myChart = null;
  },
  methods: {
    change() {
      var s = this.value.split("--")[0];
      var e = this.value.split("--")[1];
      
      this.init(s, e);
    },
    option() {
      var userList = this.sessionData("get", "userList");
      if (userList && userList.length) {
        var len = Math.ceil(userList.length / 20);
        for (var i = 0; i < len; i++) {
          this.options.push({
            value: i * 20 + "--" + (i * 20 + 20),
            label: i * 20 + "--" + (i * 20 + 20) + "名"
          });
        }
      }
    },
    init(s, e) {
      level({
        start: s || 0,
        end: e || 20
      }).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          var user = [];
          var data = [];
          for (var i in list) {
            user.push(list[i].name);
            data.push((list[i].rate || 0).toFixed(2));
          }
          this.loadChart(user, data);
          for(var i in data){
            data[i] = (Number(data[i])*100)
          }
        }
      });
    },
    loadChart(user, data) {
      this.$nextTick(() => {
        echarts.registerTheme("westeros", echartsTheme);
        this.myChart = echarts.init(
          document.getElementById(this.id),
          "westeros"
        );
        this.myChart.setOption(this.initOption(user, data));
      });
    },
    initOption(user, data) {
      return {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          },
        },
        grid: {
          top: "15%",
          right: "3%",
          left: "5%",
          bottom: "12%"
        },
        xAxis: [
          {
            type: "category",
            data: user,
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.12)"
              }
            },
            axisLabel: {
              margin: 10,
              color: "#e2e9ff",
              textStyle: {
                fontSize: 14
              }
            }
          }
        ],
        yAxis: [
          {
            axisLabel: {
              formatter: "{value}",
              color: "#e2e9ff"
            },
            name: "%",
            min: 0,
            max: 100,
            interval: 20,
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.12)"
              }
            }
          }
        ],
        series: [
          {
            type: "bar",
            data: data,
            barWidth: "20px",
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(0,244,255,1)" // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "rgba(0,77,167,1)" // 100% 处的颜色
                    }
                  ],
                  false
                ),
                barBorderRadius: [30, 30, 30, 30],
                shadowColor: "rgba(0,160,221,1)",
                shadowBlur: 4
              }
            },
            label: {
              normal: {
                show: true,
                lineHeight: 30,
                width: 40,
                height: 30,
                backgroundColor: "rgba(0,160,221,0.1)",
                borderRadius: 200,
                position: ["-8", "-60"],
                distance: 1,
                formatter: ["    {d|}", " {a|{c}}     \n", "    {b|}"].join(","),
                rich: {
                  d: {
                    color: "#3CDDCF"
                  },
                  a: {
                    color: "#fff",
                    align: "center"
                  },
                  b: {
                    width: 1,
                    height: 30,
                    borderWidth: 1,
                    borderColor: "#234e6c",
                    align: "left"
                  }
                }
              }
            }
          }
        ]
      };
    }
  },
  watch: {}
};
</script>

<style lang="less">
.sl{
  position: absolute;
  top: 90px;
  left: 27px;
  z-index: 6;
  color:#fff,
}
.sl span{
  width: 15px;
  height: 10px;
  border-radius: 4px;
  left:20px;
  display: inline-block;
  background: #52e5ff;
}
.orderAreaWrapper {
  width: 100%;
  height: 480px;
  background: url("../../assets/img/ztpm.png") no-repeat;
  background-size: 100% 100%;
}
.changeData {
  padding-left: 52px;
  position: relative;
  top: 48px;
  z-index: 1;
}
#barChart {
  width: 100%;
  height: 400px;
  margin-top: 54px;
}
/deep/.el-input__inner {
  border-radius: 0px;
}
</style>
